/*pop up*/

/*modal*/

#popup {
  visibility: hidden;
  opacity: 0;
  margin-top: -300px;
}
#popup:target {
  visibility:visible;
  opacity: 1;
  background-color: rgba(221, 217, 217, 1);
  position: fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  margin:0;
  z-index: 999;
  transition:all 1s;
}
.popup-contenedor {
  position: relative;
  margin:7% auto;
  padding:30px 50px;
  background-color: #fafafa;
  color:#333;
  border-radius: 15px;
  width:40%;

}

a.popup-cerrar {
  position: absolute;
  top:30px;
  left:30px;
  background-color: #333;
  padding:7px 10px;
  font-size: 20px;
  text-decoration: none;
  line-height: 1;
  color:#fff;
}

.popleft{
  width: 50%;
  float: left;
 
}

.popright{
  width: 50%;
  float: right;
  padding-left: 20px;
}

.popimg{
  max-width: 350px;
